import React from 'react';
import { Dropdown, getJson, storageAvailable } from '../utility.js';

var translations = {
    test: null
}

export default class LanguageSelector extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            lang: 'English'
        };

        this.handleChange = this.handleChange.bind(this);
    }

    componentDidMount() {

    }

    handleChange(value) {
        this.setState({
            lang: value
        });
    }

    translate(string) {
    
    }

    render() {
        let langSelect = <Dropdown id="BuildSelectDropdown"
                            options={['English', 'Test']}
                            value={this.state.lang}
                            onChange={this.handleChange} />;
        return (
            <div className="language-selector">
                <div className="select">{langSelect}</div>
            </div>
        )
    }
}


